<template>
  <!---->
  <div class="one" ref="oneBox" @mousemove="mouseMove">
    <!---->
    <div class="header">
      <div class="name">
        <div class="logo">
          <img src="../assets/images/logo.png" alt="LOGO" />
        </div>

        <div class="description">
          <div class="name-en">
            <div class="four">4</div>
            <div class="zero-yun">
              <div class="zero">0</div>
              <div class="yun">yun</div>
            </div>
          </div>

          <div class="way" id="test"></div>

          <div class="way-en">A website showing university training works</div>
        </div>
      </div>
    </div>

    <!---->
    <div class="data">
      <div class="works">
        <div class="title"></div>

        <div class="value">{{works}}</div>
      </div>

      <div class="views">
        <div class="title"></div>

        <div class="value">{{views}}</div>
      </div>

      <div class="author">
        <div class="title"></div>

        <div class="value">J<span class="small">effrey</span></div>
      </div>
    </div>

    <!---->
    <div class="container">
      <h2></h2>
      <section>
        <p>
          2018
          <img id="ying" src="../assets/images/ying_1.png" alt="" />
          CJavaflash3DIOS
        </p>
        <p>WebWeb</p>
        <p>htmlcssjsjsAJAXjqueryhtml5css3ES6angularJSvueJSnodeJSmongodbjsptomcatjava web......C3()</p>
      </section>
    </div>
  </div>
</template>

<script>
  import TWEEN from '@tweenjs/tween.js'

  export default {
    name: 'one',
    data () {
      return {
        msg: '',
        works: 0,
        views: 0,
        yingEle: {},
        oneBoxWidth: 0,
        tweeningValue: 0
      }
    },
    computed: {},
    created () {
      this.$store.commit('addViews');
    },//vmdata<template></template>
    mounted () {
      let _works = 20,
          _views = 0,
          self = this;

      setTimeout(()=>{
        _views = this.$store.state.views;

        new TWEEN.Tween({ tweeningNumber: 0 })
          .easing(TWEEN.Easing.Quadratic.Out)
          .to({ tweeningNumber: _works }, 3000)
          .onUpdate(function (object) {
            self.works = object.tweeningNumber.toFixed(0)
          })
          .start();
        new TWEEN.Tween({ tweeningNumber: 0 })
          .easing(TWEEN.Easing.Quadratic.Out)
          .to({ tweeningNumber: _views }, 5000)
          .onUpdate(function (object) {
            self.views = object.tweeningNumber.toFixed(0)
          })
          .start();
        this.animate();
      }, 500);

      this.yingEle = document.querySelector('#ying');
    },
    methods: {
      mouseMove(e){
        this.oneBoxWidth = this.$refs.oneBox.getBoundingClientRect().width;

        if (e.clientX<0 || e.clientX>this.oneBoxWidth) return false;

        this.tweeningValue = Math.floor(57 - 56*(e.clientX/this.oneBoxWidth));
        new TWEEN.Tween({ tweeningMarginLeft: 0 })
          .to({ tweeningMarginLeft: this.tweeningValue }, 500)
          .easing(TWEEN.Easing.Quadratic.Out)
          .onUpdate(()=>{
            this.yingEle.style.setProperty('margin-left', this.tweeningValue + 'px');
          })
          .start();
        this.animate();
      },
      animate () {
        if (TWEEN.update()) requestAnimationFrame(this.animate);
      }
    },
    props: {},
    watch: {},
    filters: {},//filterFun: value => {  return value  }  -> {{ msg | filterFun }}
    components: {
      //      
    }
  }
</script>

<style lang="scss" scoped>
  @import '~@/assets/scss/const.scss';
  @import '~@/assets/scss/mixin.scss';

  .one {
    width: 80%;
    margin: 50px auto 0;
    height: 100%;
    height: 100vh;
    .header {
      @include child-center('y');
      color: $color-theme;
      animation: header-ani 2s ease-out;
      .name {
        display: flex;
        flex: 1;
        .logo {
          @include child-center;
        }

        .description {
          font-size: $font-14px;
          .name-en {
            display: flex;
            .four {
              display: flex;
              font-size: 35.5px;
            }

            .zero-yun {
              .zero {
                padding-top: 3px;
              }
            }
          }
        }
      }
    }

    .data {
      display: flex;
      justify-content: space-around;
      padding: $outer-between*2 0;
      color: #3C1642;
      &>div {
        @include child-center;
        flex-direction: column;

        .value {
          font-size: 72px;
          .small {
            font-size: $font-14px;
          }
        }
      }
    }

    .container {
      column-count: 3;
      color: $color-theme;
      animation: container-ani 2s ease-out;
      h2 {
        column-span: 1;
        padding-bottom: $inner-between;
      }
      section {
        text-indent: 2rem;

        #ying {
          width: 50px;
          height: auto;
          float: left;
          shape-outside: inset(12px 0 -25px round 50%);
          margin-left: 0;
        }
      }
    }

  }

  @media screen and (max-width: 768px){
    .logo {
      max-width: 230px;
      min-width: 100px;
      width: 50%;
      img {
        width: 100%;
        height: auto;
      }
    }
  }

  @keyframes header-ani {
    0% {
      opacity: 0;
      transform: translateY(-100px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }

  @keyframes container-ani {
    0% {
      opacity: 0;
      transform: translateY(100px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
</style>



