<template>
  <!---->
  <div class="two-home" :style="{ 'height': $store.getters.towBoxHeight + 'px' }">
    <el-container>
      <!---->
      <el-header>{{ title }}</el-header>

      <!---->
      <el-main>
        <el-carousel :interval="3000" type="card" :height="($store.getters.towBoxHeight-127)+'px'" @change="getCurIndex">
          <el-carousel-item v-for="(item, index) in categoryList" :key="index">
            <router-link :to="'./works?category='+item.name+'&categoryId='+index">
              <div class="img"></div>

              <p class="txt" id="carouselTxt">{{ item.introduce }}</p>
            </router-link>
          </el-carousel-item>
        </el-carousel>
      </el-main>
    </el-container>
  </div>
</template>

<script>
  //  import {  } from ''
  //  import  from '@/components/'

  export default {
    name: 'two-home',
    data () {
      return {
        title: '',
        categoryList: [
          {
            name: 'word',
            category: '',
            introduce: ''
          }, {
            name: 'ppt',
            category: 'PPT',
            introduce: 'APPAPP'
          }, {
            name: 'my-video',
            category: '',
            introduce: '3Dpaly'
          }, {
            name: 'flash',
            category: 'Flash',
            introduce: 'AS'
          }, {
            name: 'exe',
            category: 'exe',
            introduce: 'C++-'
          }, {
            name: 'web',
            category: '',
            introduce: 'php24'
          }, {
            name: 'three-d',
            category: '',
            introduce: ''
          }, {
            name: 'app',
            category: 'APP',
            introduce: 'appAPP'
          }, {
            name: 'design',
            category: '',
            introduce: ''
          }, {
            name: 'java',
            category: 'Java',
            introduce: ''
          }
        ]
      }
    },
    computed: {
      curKsfpIndex(){
        return this.$store.state.curKsfpIndex;
      }
    },
    created () {},//vmdata<template></template>
    mounted () {},//domajax
    methods: {
      getCurIndex(index){
        this.title = this.categoryList[index].category;
      }
    },
    props: {},
    watch: {
      curKsfpIndex(newVal, oldVal){

      }
    },
    filters: {},//filterFun: value => {  return value  }  -> {{ msg | filterFun }}
    components: {
      //      
    }
  }
</script>

<style lang="scss" scoped>
  @import '~@/assets/scss/const.scss';
  @import '~@/assets/scss/mixin.scss';

  .two-home {
    &>.el-container {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      .el-header {
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        line-height: 60px;
        font-size: $font-20px;
        font-weight: bold;
      }

      .el-main {
        @include scrollbar;
        .el-carousel__item {
          a {
            @include child-center;
            flex-direction: column;
            width: 100%;
            height: 100%;
            color: #000;
            .img {
              width: 100%;
              flex: 7.5;
            }

            .txt {
              @include ellipsis(3);
              width: 100%;
              box-sizing: border-box;
              padding: $inner-between;
              flex: 2.5;
              text-indent: 2rem;
              background-color: #fff;
              line-height: 1.67;
            }
          }

          &:nth-child(2n) a .img {
            background-color: #99a9bf;
          }

          &:nth-child(2n+1) a .img {
            background-color: #d3dce6;
          }
        }
      }
    }
  }

  @media screen and (max-width: 1080px) {
    #carouselTxt {
      @include ellipsis(2);
      line-height: 1.8;
    }
  }
</style>



